@include font(Roboto-Black, Roboto-Black);
@include font(Roboto-Light, Roboto-Light);
@include font(Roboto-Regular, Roboto-Regular);
@include font(Roboto-Thin, Roboto-Thin);
@include font(Roboto-Medium, Roboto-Medium);

body {
	box-sizing: border-box; }
.center {
	width: 70%;
	margin: 0 auto;
	max-width: 1360px;
	position: relative;
	@include clr; }
.circle {
	width: 15px;
	height: 15px;
	background-color: #6e8079;
	display: inline-block;
	border-radius: 50%;
	margin: 5px;
	cursor: pointer;
	@include r(660) {
		margin: 1px; } }
.left {
	float: left;
	width: 45%;
	@include r(860) {
		width: 100%;
		float: none;
		text-align: center;
		margin-bottom: 50px; } }
.right {
	float: right;
	width: 45%;
	@include r(860) {
		width: 100%;
		float: none; } }
.menu {
	width: 45px;
	height: 45px;
	background-color: #26a95d;
	float: right;
	border-radius: 5px;
	cursor: pointer;
	display: none;
	@include r(660) {
		display: block; }
	span {
		width: 80%;
		height: 3px;
		background-color: #fff;
		display: block;
		margin: 9px auto;
		position: relative;
		transition: .3s ease-in all; } }
.open {
	span:nth-child(1) {
		transform: rotate(45deg);
		top: 12px; }
	span:nth-child(2) {
		transform: rotate(-45deg); }
	span:nth-child(3) {
		transform: rotate(180deg);
		opacity: 0; } }
.hidden {
	opacity: 0; }
