.header {
	height: 45px;
	background-color: #2c3e50;
	@include clr; }
.logo {
	width: 140px;
	height: 100%;
	background-color: #fff;
	text-align: center;
	color: #27ae60;
	text-transform: uppercase;
	line-height: 45px;
	font-family: "Roboto-Black";
	font-size: 25px;
	float: left; }
.nav {
	float: left;
	width: 75%;
	@include r(1360) {
		width: 70%; }
	@include r(1120) {
		width: 60%; }
	@include r(860) {
		width: 55%; }
	@include r(750) {
		width: 40%; }
	@include r(660) {
		position: absolute;
		right: 0;
		top: 45px;
		display: block;
		height: 200px;
		background-color: #2c3e50;
		float: none;
		width: 50%;
		z-index: 5;
		transition: .3s ease-in all;
		opacity: 0; } }
.nav__list {
	margin-left: 40%;
	@include r(1120) {
		margin-left: 30%; }
	@include r(860) {
		margin-left: 20%; }
	@include r(750) {
		margin-left: 10px; } }
.nav__item {
	display: inline-block;
	margin-right: 40px;
	@include r(860) {
		margin-right: 10px; }
	@include r(660) {
		width: 100%;
		display: block; } }

.nav__link {
	color: #fff;
	text-decoration: none;
	font-family: "Roboto-Regular";
	line-height: 45px;
	font-size: 15px; }
.enter {
	width: 90px;
	height: 100%;
	float: right;
	@include r(660) {
		position: absolute;
		top: 200px;
		right: 0;
		z-index: 5;
		right: 60px;
		opacity: 0;
		transition: .3s ease-in all; }
	@include r(470) {
		right: 30px; } }
.enter__btn {
	width: 100%;
	height: 35px;
	margin-top: 5px;
	border-radius: 5px;
	background-color: #239d57;
	color: #fff;
	text-transform: uppercase;
	font-family: "Roboto-Medium";
	font-size: 15px; }
.advantages {
	overflow: hidden;
	position: relative;
	.center {
		position: absolute;
		left: 15%;
		z-index: 1; } }
.prev {
	width: 20px;
	height: 35px;
	cursor: pointer;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	left: 0; }
.next {
	width: 20px;
	height: 35px;
	cursor: pointer;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	right: 0; }
.slider {
	width: 9999px;
	@include clr; }
.slider__item {
	float: left;
	transition: .5s ease-in all;
	position: relative;
	.center {
		position: absolute;
		top: 0;
		z-index: 0;
		@include clr; } }
.slider__img {
	width: 100%;
	min-height: 440px;
	max-height: 680px;
	@include r(760) {
		opacity: 0.6; } }
.slider__info {
	width: 350px;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	right: 200px;
	@include clr;
	@include r(1360) {
		right: 100px; }
	@include r(1120) {
		right: 20px; }
	@include r(760) {
		right: 10%; }
	@include r(660) {
		display: block;
		margin: 0 auto; }
	@include r(520) {
		right: 0;
		left: 0px; }
	@include r(440) {
		left: -40px; } }
.slider__text {
	float: right;
	font-family: "Roboto-Thin";
	font-size: 55px;
	line-height: 65px;
	color: #555555;
	text-align: center;
	font-weight: 300;
	margin-bottom: 30px;
	@include r(760) {
		font-weight: 700; }
	@include r(570) {
		font-size: 40px;
		display: block;
		width: 80%;
		float: none;
		margin: 0 auto; }
	@include r(520) {
		font-size: 30px;
		width: 40%;
		word-wrap: break-word; } }
.slider__text_white {
	color: #fff; }
.slider__btn {
	width: 80px;
	min-height: 40px;
	background-color: #239d57;
	font-family: "Roboto-Regular";
	color: #fff;
	text-transform: uppercase;
	font-size: 20px;
	margin: 0 auto;
	display: block; }
.slider__choose {
	position: absolute;
	left: 45%;
	bottom: 20px;
	.active {
		background-color: #2c3e50; }
	@include r(760) {
		left: 38%; }
	@include r(620) {
		left: 35%; } }
.blog {
	padding: 40px 0 80px 0; }
.blog__caption {
	font-family: "Roboto-Light";
	font-weight: 400;
	font-size: 35px;
	line-height: 60px; }
.blog__text {
	font-family: "Roboto-Light";
	font-size: 15px;
	line-height: 25px;
	color: #444444; }
.blog__sign {
	display: block;
	font-family: "Roboto-Light";
	font-size: 11px;
	color: #2c3e50;
	width: 485px;
	float: right;
	@include r(1350) {
		width: 385px; }
	@include r(1075) {
		width: 285px; }
	@include r(860); }

.blog__img {
	width: 485px;
	height: 480px;
	float: right;
	@include r(1350) {
		width: 385px; }
	@include r(1075) {
		width: 285px;
		height: 380px; }
	@include r(860) {
		width: 100%; } }
.register {
	padding: 80px 0 85px 0;
	background-image: url("../img/bgi.jpg");
	background-size: 100% 100%;
	background-repeat: no-repeat; }
.form-reg {
	width: 460px;
	padding: 0 80px 50px 80px;
	background-color: #fff;
	margin: 0 auto;
	@include r(640) {
		width: 300px;
		padding: 0 20px 20px 20px; }
	@include r(400) {
		width: 250px; } }
.form-reg__caption {
	text-align: center;
	padding: 25px 0;
	font-family: "Roboto-Thin";
	font-size: 35px;
	line-height: 32px;
	color: #555555;
	font-weight: 500; }
.form-reg__input {
	width: 100%;
	height: 50px;
	margin-bottom: 10px;
	background-color: #f5f5f5;
	border-radius: 2px;
	border-top: 1px solid lightgrey;
	padding: 0 0 0 20px;
	font-family: "Roboto-Light";
	font-size: 20px;
	line-height: 32px;
	color: #555555; }
.enter__btn_center {
	float: none;
	display: block;
	margin: 0 auto;
	height: 50px; }
.footer {
	height: 45px;
	text-align: center;
	background-color: #2c3e50; }
.copyrights {
	color: #fff;
	font-family: "Roboto-Light";
	font-size: 15px;
	line-height: 45px; }
